@import url(https://fonts.googleapis.com/css2?family=Mali:wght@400;600&display=swap);
body{background-color:#282c34;margin:0;font-family:'Mali', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-weight:400;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}.page{margin-top:40px;margin-bottom:40px}.colorize{color:yellow}.page{width:100%;max-width:1000px}.question{background-color:rgba(0,0,0,0.2);margin:40px;padding:40px;border-radius:8px}.question.optional{background-color:rgba(0,0,0,0.1);border:1px dashed rgba(255,255,255,0.5)}.question .qtitle{font-weight:bold}@media only screen and (min-width: 600px){.question .boxbox{display:grid;grid-gap:40px;grid-template-columns:1fr 1fr}}

.boxes{display:grid;grid-template-columns:2fr 1fr;grid-template-rows:1fr 2fr 1fr;margin-left:auto;margin-right:auto;grid-gap:4px}.boxes>div{display:flex;justify-content:center;align-items:center;border-radius:8px;cursor:pointer;opacity:0.5;padding:10px}.boxes .highlight{box-shadow:inset 0px 0px 0px 2px white;opacity:1}

